<template>
  <div class="auth-step2">
    <div class="view-box">
      <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="240" height="240" viewbox="0 0 240 240">
        <circle cx="120" cy="120" r="115" stroke-width="10" stroke="#D1D3D7" fill="none" ></circle>
        <circle cx="120" cy="120" r="115" stroke-width="10" stroke="#00A5E0" fill="none" transform="matrix(0,-1,1,0,0,240)" stroke-dasharray="0 10"></circle>
      </svg>
    </div>
    <button @click="tapClick">click</button>
    这是第二步
  </div>
</template>

<script>
  export default {
    name: "auth-step2",
    data() {
      return {
        current: 0
      }
    },
    methods: {
      tapClick() {
        console.log('click!')
        var range = document.querySelector("#range"), circle = document.querySelectorAll("circle")[1];
        var percent = Math.random() * 100 / 100, perimeter = Math.PI * 2 * 170;
        circle.setAttribute('stroke-dasharray', perimeter * percent + " " + perimeter * (1- percent));
      }
    }
  }
</script>

<style lang="less" scoped>
  .auth-step2 {
    .view-box {
      position: relative;
      width: 240px;
      height: 240px;
    }
    circle {
      transition: stroke-dasharray .25s;
    }
  }
</style>



